import React from "react"
import favicon from "../../../public/favicon.png"
import { Helmet } from "react-helmet-async"

const Wrapper = ({ children }) => {
  return (
    <>
      <Helmet>
        <link rel="icon" type="image/png" href={ favicon }/>
      </Helmet>
      { children }
    </>
  )
}
export default Wrapper
